.pagination { 
    margin-top: 80px;
    margin-bottom: -15px;

    // @include mq('tablet'){
    //     margin-top: 80px;
    // }
    &__list {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
    }

    &__item {
        background-color: $color-lgt-blue-4;
        height: 50px;
        min-width: 50px;
        border-radius: 25px;
        margin-bottom: 15px;
        &:not(:last-of-type) {
            margin-right: 12px;
        }

        &.-sp{
            display: none;
            @include mq('tablet'){
                display: block;
            }
        }

        a {
            display: flex;
            justify-content: center;
            align-items: center;
            font-size: 16px;
            line-height: 1.5;
            color: $color-primary;
            background-color: $color-lgt-blue-4;
            text-decoration: none;
            height: 100%;
            border-radius: 25px;
            padding: 7px;
            transition: all 0.25s ease;

            &:hover {
                background-color: #D2E8FC;
            }
        }

        &.-dots {
            background-color: unset;
            min-width: auto;
            margin: 0 20px 0 8px;
            a {
                background-color: unset;
                color: $color-black;
                padding-left: 0;
                padding-right: 0;
            }
        }

        &.-active {
            background-color: #D2E8FC;

            a {
                background-color: #D2E8FC;
                color: $color-primary;
            }
        }

        &.-first,
        &.-next{
            margin-right: 4px;
        }

        &.-next a {
            img{
                transform: rotate(180deg);
            }
        }

        &.-next,
        &.-last,
        &.-first,
        &.-prev{
            display: none;

            @include mq("tablet"){
                display: inline-block;
            }
        }
    }
}