.mission {
    @include mq("tablet") {
        padding-bottom: 120px;
    }

    &__wrapper {
        position: relative;

        @include mq("tablet", "max") {
            display: flex;
            flex-direction: column-reverse;
            gap: 45px;
        }
    }

    &__contents {
        width: 100%;

        @include mq("desktop") {
            max-width: 512px;
            flex-basis: 512px;
            width: 100%;
        }
    }

    &__img {
        text-align: center;

        @include mq("tablet") {
            text-align: unset;
            position: absolute;
            right: 0;
            top: 95px;
            z-index: 0;
            width: 50%;
        }
        
        @include mq("bootstrap-lg") {
            top: 5px;
            width: 60%;
            
        }
        
        @include mq("desktop") {
            width: auto;
        }
    }

    &__descwpr {
        @include mq("tablet") {
            max-width: 352px;
            width: 65%;
        }
        @include mq("bootstrap-lg") {
            max-width: 352px;
            width: 70%;
        }
        @include mq("desktop") {
            max-width: 352px;
        }
    }

    .paragraph {
        padding-bottom: 40px;
    }

    .pagettl-bar {
        text-align: left;
        letter-spacing: 0;
        margin: 0;
        line-height: 1.4;
        padding-bottom: 50px;
        padding-top: 0;
        @include mq("tablet") {
            line-height: 1.5;
            padding-bottom: 79px;
        }
    }

    .subttl {
        @include mq("tablet", "max") {
            line-height: 1.909;
            padding-bottom: 37px;
        }
    }
}