.footer {
    margin-top: auto;
    
    &__wpr {
        padding: 40px 0;
        background-color: $color-gray-4;
        margin-top: 120px;
    }
    .container {
        @include mq("phablet", "max") {
            // padding: 0;
        }
    }

    &.-top {
        .footer__wpr {
            margin-top: 40px;
        }
    }

    &__inner {
        display: flex;
        justify-content: center;
        align-items: center;
        font-weight: 400;
        color: $color-white;
        // padding: 0 22px;
        margin: 0 -20px;

        @include mq("tablet") {
            margin: 0;
            justify-content: space-between;
            padding: 0 22px;
            padding: 0;
        }
    }

    &__txt {
        font-size: 1.1rem;
        text-align: end;

        @include mq("tablet") {
            text-align: start;
        }
    }

    &__lnk {
        font-size: 1.3rem;
        color: $color-white;
        transition: all 0.25s ease;
        display: none;

        @include mq("tablet") {
            display: inline;

            &:hover {
                opacity: 0.7;
                text-decoration: none;
            }
        }
    }
}
