.custom-radio {
    display: flex;
    align-items: center;
}

.form-radio {
    display: flex;
    align-items: center;
    position: relative;
    margin-right: 15px;

    &__input {
        position: absolute;
        z-index: -1;
        left: 0;
        visibility: hidden;

        &:checked ~ .form-radio__label .radio-box::after {
            background-image: url(../img/icons/icon-check.svg);
            opacity: 1;
        }
    }

    &__label {
        display: flex;
        align-items: center;
        cursor: pointer;
    }

    .radio-box {
        height: 18px;
        width: 18px;
        min-width: 18px;
        border-radius: 50%;
        position: relative;

        &:after {
            content: "";
            position: absolute;
            background-image: url(../img/icons/radio-unchecked.svg);
            background-repeat: no-repeat;
            background-size: cover;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            height: 20px;
            width: 20px;
            min-width: 20px;
            border-radius: 50%;
            // background-color: $color-gray;
            // opacity: 0;
        }
    }
}
