.custom-row {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -8px 24px;

    &.-mb0 {
        margin: 0 -8px -16px;
    }

    &.-m-sm-inline-5 {
        @include mq("tablet", "max") {
            margin: 0 -5px 0;
        }
    }

    .col {
        margin-bottom: 16px;
        padding: 0 8px;
        flex-grow: 0;

        @include mq("tablet") {
            margin-bottom: 16px;
        }

        &:last-child {
            margin-bottom: 0;
        }

        &.-col3 {
            width: 100%;
            flex-basis: 100%;

            @include mq("tablet") {
                width: 33.33%;
                flex-basis: 33.33%;
            }

            &:not(:last-of-type) {
                @include mq("tablet", "max") {
                    margin-bottom: 20px;
                }
            }
            .services__img {
                img {
                    aspect-ratio: 330 / 113;
                    @include mq("tablet") {
                        aspect-ratio: 350 / 120;
                    }
                }
            }
        }

        &.-col4 {
            width: 50%;
            flex-basis: 50%;

            @include mq("tablet") {
                width: 25%;
                flex-basis: 25%;
            }

            @include mq("tablet", "max") {
                padding: 0 5px;
            }

            .services__img {
                img {
                    aspect-ratio: 160 / 83;
                    @include mq("tablet") {
                        aspect-ratio: 260 / 135;
                    }
                }
            }
        }
    }
}
