.activity {
    overflow-x: hidden;
    overflow-y: visible;
    background-color: $color-lgt-blue-4;
    position: relative;
    // padding-top: 180px;
    // margin-top: -180px;

    .article__col {
        width: 100%;
        padding: 0;
        margin-bottom: 0;
    }

    &__box {
        margin-bottom: 40px;
    }

    &-swiper {
        overflow: visible;
    }

    &__box {
        position: relative;

        &::after {
            content: '';
            height: 100%;
            width: 100vw;
            position: absolute;
            right: 100%;
            top: 0;
            background-color: $color-lgt-blue-4;
            z-index: 5;
        }
    }

    .article-card__img {
        @include mq("tablet") {
            margin-bottom: 15px;
        }
    }
}