  }
}
.footer__txt {
  font-size: 1.1rem;
  text-align: end;
}
@media only screen and (min-width: 768px) {
  .footer__txt {
    text-align: start;
  }
}
.footer__lnk {
  font-size: 1.3rem;
  color: #ffffff;
  transition: all 0.25s ease;
  display: none;
}
