.tbl {
    margin-bottom: 30px;
    &-flex {
        display: flex;
        flex-direction: column;
        background-color: $color-white;
        @include mq("tablet") {
            flex-direction: row;
        }
    }
    &-txt {
        &:first-child {
            // width: 100%;
            border-top: solid 1px $color-primary;
            color: $color-primary;
            @include mq("tablet", "max") {
                width: 100%;
            }
            @include mq("tablet") {
                min-width: 168px;
                max-width: 60%;
                // width: 100%;
                flex-shrink: 0;
            }
            @include mq("bootstrap-lg"){
                max-width: 80%;
            }
        }
        &:last-child {
            width: 100%;
            border-top: solid 1px $color-gray-2;
            color: $color-black;
            flex-grow: 1;
            @include mq("tablet") {
                width: 20%;
            }
        }
    }
    p {
        padding: 15px 10px;
        line-height: 1.5;
        margin-bottom: 0;
    }
}
