.btn {
    &-line {
        font-size: 1.6rem;
        font-weight: 400;
        color: $color-primary;
        padding: 10px 6px;
        display: inline-block;
        position: relative;
        text-decoration: none;
        transition: all 0.25s ease;

        &:after {
            position: absolute;
            content: "";
            background: linear-gradient(90deg, rgba(47, 174, 229, 1) 0%, rgba(0, 92, 169, 1) 100%);
            width: 100%;
            height: 2px;
            left: 0;
            bottom: 0;
            transition: all 0.25s ease;
        }

        @include mq("tablet") {
            &:hover,
            &:focus-visible {
                &:after {
                    width: 110%;
                }
            }
        }
    }

    &-sns {
        font-size: 1.6rem;
        font-weight: 400;
        color: $color-white;
        line-height: 1.6;
        display: inline-flex;
        border-radius: 4px;
        background-color: red;
        width: 100%;
        text-decoration: none;
        transition: all 0.25s ease;

        @include mq("tablet") {
            &:hover,
            &:focus-visible {
                transform: translateY(-5px);
            }
        }

        &:not(:last-child) {
            margin-bottom: 16px;
        }

        span {
            &:first-child {
                width: 46px;
                display: flex;
                justify-content: center;
                align-items: center;
                border-top-left-radius: 4px;
                border-bottom-left-radius: 4px;
            }

            &:last-child {
                display: block;
                padding: 10px 0 10px 30px;
                border-top-right-radius: 4px;
                border-bottom-right-radius: 4px;
            }
        }

        &.-black {
            background-color: $color-black-2;

            span {
                &:first-child {
                    background-color: $color-black;
                }
            }
        }

        &.-pink {
            background-color: $color-pink;

            span {
                &:first-child {
                    background-color: $color-drk-pink;
                }
            }
        }

        &.-red {
            background-color: $colr-lgt-red;

            span {
                &:first-child {
                    background-color: $color-red-2;
                }
            }
        }

        &.-blue {
            background-color: $color-drk-blue;

            span {
                &:first-child {
                    background-color: $color-drk-blue2;
                }
            }
        }
    }
}

.component {
    .wp-block-buttons {
        row-gap: 0;
    }
    .wp-block-button__link {
        font-size: 1.6rem;
        font-weight: 500;
        letter-spacing: 0.05em;
        color: $color-white;
        background-color: $color-primary;
        border: 2px solid $color-primary;
        border-radius: 6px;
        padding: 16px 55px 16px 15px;
        transition: all 0.25s ease;
        position: relative;
        line-height: 1.5;
        margin-bottom: 10px;
        text-align: left;
        text-decoration: none;
        display: inline-block;
        &:before,
        &:after {
            position: absolute;
            content: "";
            top: 50%;
            transform: translateY(-50%);
            transition: all 0.25s ease;
        }
