.section {
    padding-bottom: 100px;
}

.text-center {
    text-align: center;
}

.paragraph {
    font-size: 1.6rem;
    font-weight: 400;
    color: $color-black;
    line-height: 2;
}

.pagettl-bar {
    font-family: $font-sec;
    font-size: 2.8rem;
    letter-spacing: 0.05em;
    padding-block: 60px;

    @include mq("tablet") {
        text-align: center;
        line-height: 1.12;
        letter-spacing: 0.05em;
        padding-block: 40px 60px;
    }

    @include mq("bootstrap-lg") {
        font-size: 3.2rem;
    }

    &.-blue {
        text-align: center;
        font-size: 2rem;
        color: $color-primary;
        line-height: 1.96;
    }
}

.subttl {
    font-size: 2.2rem;
    font-weight: 400;
    color: $color-black;
    padding-bottom: 40px;

    @include mq("tablet") {
        font-size: 2.4rem;
    }
}