.hero {
    background-color: $color-lgt-blue;
    padding-bottom: 106px;
    // height: calc(100vh - 67px);
    position: relative;

    @include mq("tablet") {
        height: 100%;
        padding-bottom: 80px;
    }

    &__img {
        position: relative;

        @include mq("tablet", "max") {
            padding-right: 10px;
        }

        img {
            display: block;
            width: 100%;
        }
    }

    &__ttl {
        margin-inline: 30px;
        font-size: 2rem;
        font-weight: 700;
        line-height: 1.6;
        margin-top: 18px;

        @include mq("tablet") {
            margin-top: 0;
            font-size: 2.4rem;
            line-height: 1.5;
            color: $color-white;
            position: absolute;
            left: 40px;
            bottom: 10px;
        }

        @include mq("bootstrap-lg") {
            left: 171px;
            bottom: 41px;
            // text-shadow: -6px 6px 4px $color-black;
            text-shadow: 0 0 20px rgba(0, 0, 0, 0.16);
        }
    }

    &__scroll {
        position: absolute;
        bottom: 40px;
        right: 23px;
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 10px;

        @include mq("tablet") {
            display: none;
        }

        span {
            color: $color-blue;
            font-size: 1.1rem;
            @include mq("phablet"){
                font-size: 1.4rem;
            }
        }

        &::before {
            content: "";
            display: block;
            width: 6px;
            height: 6px;
            border-radius: 50%;
            background-color: $color-blue;
            position: absolute;
            bottom: 0;
            left: 50%;
            transform: translateX(-58%);
            animation: scrollAnimate 3s linear infinite;
            @include mq("phablet"){
                width: 10px;
                height: 10px;
            }
        }

        &::after {
            content: "";
            display: block;
            width: 1px;
            height: 90px;
            border-right: 1px dotted $color-blue;
            transform: translateX(-1px);
            @include mq("phablet"){
                border-right: 3px dotted $color-blue;
            }
        }
    }
}

@keyframes scrollAnimate {
    0% {
        bottom: 88px;
        opacity: 0;
    }

    5% {
        opacity: 0.9;
    }

    50% {
        opacity: 1;
    }

    95% {
        opacity: 0.9;
    }

    100% {
        bottom: -3px;
        opacity: 0;
    }
}