.blockaside {
    display: flex;
    flex-direction: column-reverse;
    gap: 60px;

    @include mq("tablet") {
        gap: 44px;
        flex-direction: row;
    }

    @include mq("bootstrap-lg") {
        gap: 88px;
    }

    &__acc-box {
        transition: all 0.2s ease;
        max-height: 1000px;
        padding: 40px 25px 0 25px;
        background-color: #fff;
        border: 1px solid #d2e8fc;
        border-radius: 0 0 25px 25px;

        &.closed {
            max-height: 0;
            overflow: hidden;
            padding: 0 25px;
        }

        @include mq("tablet") {
            background-color: unset;
            border-radius: 0;
            border: none;
            padding: 0;
        }
    }

    &__acc-ttl {
        font-size: 1.6rem;
        line-height: 1.5;
        letter-spacing: 0.05em;
        color: #005ca9;
        font-weight: 500;
        background-color: #d2e8fc;
        padding: 13px 13px 13px 66px;
        border-radius: 25px 25px 0 0;
        cursor: pointer;
        @include mq("tablet") {
            display: none;
        }

        position: relative;

        &::before {
            position: absolute;
            content: "";
            width: 16px;
            height: 16px;
            background-image: url(./../img/filter.svg);
            background-position: center;
            background-repeat: no-repeat;
            background-size: contain;
            left: 36px;
            top: 50%;
            transform: translateY(-50%);
        }
    }

    &__close {
        font-size: 1.2rem;
        line-height: 2;
        letter-spacing: 0.05em;
        color: #005ca9;
        text-align: center;
        padding: 13px 0 10px;
        border-top: 1px solid #d2e8fc;
        // margin-top: 40px;
        margin: 40px -25px 0 -25px;
        cursor: pointer;
        @include mq("tablet") {
            display: none;
        }

        span {
            position: relative;

            &::after {
                position: absolute;
                content: "";
                background-image: url(./../img/icons/arrow-right.svg);
                background-position: center;
                background-size: contain;
                background-repeat: no-repeat;
                width: 12px;
                height: 12px;
                transform: rotate(-90deg);
                right: -20px;
                top: 2px;
            }
        }
    }

    h3 {
        font-size: 1.8rem;
        font-weight: 500;
        letter-spacing: 0.05em;
        line-height: 1.3;
        margin-bottom: 40px;
    }

    &__lg {
        flex-grow: 1;

        &.-bgwhite {
            border-radius: 20px 0 0 20px;
            padding: 80px 60px 100px 60px;
            background-color: #fff;
            box-shadow: 3px 6px 24px rgba(154, 208, 253, 0.16);
        }

        // &.-sub{
        //     position: relative;

        //     &::after{
        //         content: '';
        //         height: 100%;
        //         width: 100vw;
        //         background-color: #fff;
        //         position: absolute;
        //         left: 100%;
        //         top: 0;

        //     }
        // }
    }

    &__sm {
        // max-width: 260px;
        // flex: 0 0 260px;
        width: 100%;

        @include mq("tablet") {
            max-width: 200px;
            flex: 0 0 200px;
        }

        @include mq("bootstrap-lg") {
            max-width: 260px;
            flex: 0 0 260px;
        }

        // &.-sm{
        //     max-width: 240px;
        //     flex: 0 0 240px;
        // }
    }

    &.-sm {
        gap: 60px;

        @include mq("tablet") {
            gap: 30px;
        }

        @include mq("bootstrap-lg") {
            gap: 60px;
        }
    }
}

.article {
    &__row {
        display: flex;
        flex-wrap: wrap;

        @include mq("tablet") {
            margin: 0 -18px;
        }
    }

    &__col {
        margin-bottom: 40px;

        &:last-of-type {
            margin-bottom: 0;
        }
        @include mq("tablet") {
            &:nth-last-of-type(2) {
                margin-bottom: 0;
            }
        }

        @include mq("tablet") {
            width: calc(100% / 2);
            padding: 0 18px;
        }
    }

    &-card {
        &__link {
            display: block;
            text-decoration: none;
            transition: all 0.25s ease;

            @include mq("tablet") {
                &:hover,
                &:focus-visible {
                    .article-card__text {
                        color: $color-primary;
                    }
                }

                &:hover,
                &:focus-visible {
                    .article-card__img img {
                        transform: scale(1.1);
                    }
                }
            }
        }

        &__img {
            // width: 100%;
            line-height: 0;
            // overflow: hidden;
            margin-bottom: 10px;
            overflow: hidden;

            img {
                aspect-ratio: 352 / 258;
                object-fit: cover;
                width: 100%;
                transform: scale(1.001);
                transition: all 0.25s ease;
            }
        }

        &__tag {
            font-size: 13px;
            font-weight: 500;
            line-height: 1.9;
            padding: 0 10px;
            // max-width: 98px;
            // width: 100%;
            margin-bottom: 4px;
            display: inline-block;
            background-color: $color-primary;
            color: #fff;
            @include mq("tablet"){
                margin-bottom: 2px;
            }
        }

        &__type {
            font-size: 13px;
            line-height: 1.96;
            color: $color-primary;
        }

        &__text {
            font-size: 16px;
            color: $color-black;
            font-weight: 500;
            line-height: 1.5;
            margin-top: 10px;
            @include line-clamp(2);
            transition: color 0.25s ease;
            @include mq("tablet"){
                margin-top: 5px;
            }
        }
    }
}

.blockcategory {
    margin-bottom: 40px;

    .component h5 {
        color: $color-primary;
        margin-bottom: 20px;

        &::before {
            background-color: $color-primary;
        }
    }

    &__list {
        li {
            &.active .blockcategory__link {
                cursor: default;
                color: $color-primary;
                padding-left: 10px;
                background-color: transparent;
                &::after {
                    @include mq("tablet") {
                        right: 10px;
                    }
                }

                &:hover,
                &:focus-visible {
                    background-color: transparent;
                }
            }
        }
    }

    &__link {
        font-size: 14px;
        line-height: 1.7;
        letter-spacing: 0.05em;
        color: $color-black;
        display: block;
        text-decoration: none;
        padding: 6px 18px 6px 0;
        position: relative;
        transition: all 0.35s ease;

        &::after {
            content: url(.././img/icons/arrow-right.svg);
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            right: 20px;
            transition: all 0.35s ease;
        }

        &:hover,
        &:focus-visible {
            color: $color-primary;
            padding-left: 10px;
            background-color: $color-lgt-blue-4;
            &::after {
                @include mq("tablet") {
                    right: 10px;
                }
            }
        }

        &.-nopadding {
            padding: 0;
            margin-top: 32px;
            display: inline-block;

            &::after {
                content: none;
            }
        }

        &.-clr-pri {
            color: $color-primary;
        }
    }

    &.-tag {
        margin-bottom: 0;

        .blockcategory {
            &__list {
                display: flex;
                flex-wrap: wrap;
                gap: 5px;
                li{
                    &.active{
                        .blockcategory__link{
                            background-color: #AECEEB;
                            color: $color-black;
                            padding: 2px 5px;
                        }
                    }
                }
            }

            &__link {
                font-size: 1.3rem;
                line-height: 1.8;
                letter-spacing: 0;
                background-color: $color-blue-2;
                padding: 2px 5px;
                border-radius: 2px;

                &::after {
                    display: none;
                }

                &:hover,
                &:focus-visible {
                    color: $color-black;
                    background-color: #aeceeb;
                }
            }
            
        }
    }
}
