.breadcrumb {
    padding: 20px 0;
    display: none;
    @include mq("tablet"){
        display: block;
    }
    &__list {
        display: flex;
        justify-content: flex-end;
        align-items: center;
        white-space: nowrap;
        overflow-x: auto;
        overflow-y: hidden;

        @include mq("phablet", "max") {
            overflow-x: scroll;
        }
    }

    &__item {
        font-size: 1.1rem;
        font-weight: 400;
        position: relative;
        padding: 0 16px;
        color: $color-primary;

        &:first-child {
            padding-left: 0;
        }

        &:last-child {
            padding-right: 0;
        }

        &:not(:last-child) {
            &::after {
                position: absolute;
                content: "/";
                top: 50%;
                transform: translateY(-50%);
                right: -2px;
                font-size: 1.1rem;
                font-weight: 400;
                color: $color-drk-gray;
            }

            // @include mq("tablet"){
            //     padding-left: 25px;
            // }
        }
    }

    &__link {
        text-decoration: none;
        color: $color-drk-gray;
        transition: opacity 0.25s ease;
        &.active {
            color: $color-primary;
            pointer-events: none;
        }
        @include mq("tablet") {
            &:hover {
                opacity: 0.7;
            }
        }
    }
}
