.services {
    padding-top: 100px;

    &__wrapper {
        &:not(:last-of-type) {
            @include mq("tablet", "max") {
                margin-bottom: 40px;
            }
        }
    }

    &__img {
        display: block;
        transition: all 0.25s ease;
        position: relative;
        width: 100%;

        img {
            width: 100%;
            aspect-ratio: 352 / 120;
            display: block;
            object-fit: cover;
        }

        @include mq("tablet") {

            &:hover,
            &:focus-visible {
                opacity: 0.7;
            }
        }
    }
}