.news {
    &__wrapper {
        max-width: 904px;
        margin: 0 auto;
    }

    &__list {
        margin-bottom: 40px;

        @include mq("tablet") {
            margin-bottom: 30px;
        }
    }

    &__itm {
        margin: 0;
        padding: 0;
        list-style-type: none;

        &:first-child {
            a {
                padding-top: 0;
            }
        }
    }

    &__tag-wpr {
        display: flex;
        align-items: center;
        gap: 30px;

        @include mq("tablet") {
            width: 250px;
        }
    }

    &__lnk {
        display: block;
        padding: 20px 0;
        border-bottom: 1px solid $color-bdr;
        color: $color-black;
        text-decoration: none;

        @include mq("tablet") {
            display: flex;
            flex-wrap: wrap;
            align-items: center;

            &:hover,
            &:focus-visible {
                .news__desc {
                    color: $color-primary;
                }
            }
        }

        @include mq("tablet") {
            flex-wrap: nowrap;
        }
    }

    &__date {
        font-size: 1.3rem;
        font-weight: 400;
        color: $color-black;
    }

    &__tag {
        padding: 4px 8px;

        color: $color-white;
        font-size: 1.3rem;
        font-weight: 400;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        position: relative;
        z-index: 4;
        // &.-blue {
        //     background: linear-gradient(90deg, rgba(47, 174, 229, 1) 0%, rgba(0, 92, 169, 1) 100%);
        //     background: linear-gradient(90deg, rgba(47, 174, 229, 1) 0%, rgba(0, 92, 169, 1) 100%);
        // }

        // &.-green {
        //     background: rgba(59, 161, 149, 1);
        //     // background: linear-gradient(90deg, rgba(192, 217, 212, 1) 0%, rgba(59, 161, 149, 1) 100%);
        // }
    }

    &__tagbg {
        background-color: $color-primary;
        position: relative;

        &:after {
            position: absolute;
            content: "";
            background: linear-gradient(90deg, rgba(255, 255, 255, 0.5) 0%, rgba(255, 255, 255, 0) 100%);
            // background: $color-primary;
            width: 100%;
            height: 100%;
            left: 0;
            top: 0;
            z-index: 3;
        }

        &.-blue {
            &::after {
                background: linear-gradient(90deg, rgba(255, 255, 255, 0.5) 0%, rgba(255, 255, 255, 0) 100%);
            }
        }
        &.-green {
            &::after {
                background: linear-gradient(90deg, rgba(192, 217, 212, 1) 0%, rgba(59, 161, 149, 1) 100%);
            }
        }
    }

    &__desc {
        flex-grow: 1;
        font-size: 1.6rem;
        font-weight: 400;
        padding-top: 6px;
        line-height: 1.5;
        @include line-clamp(2);
        transition: color 0.25s ease;

        @include mq("tablet") {
            width: calc(100% - 250px);
            @include line-clamp(1);
            padding-top: 0;
            padding-left: 10px;
        }
    }

    &__btn {
        text-align: right;
    }

    &.-list {
        .news__tag-wpr {
            @include mq("tablet") {
                width: 176px;
            }
        }

        .news__desc {
            @include mq("tablet") {
                width: calc(100% - 176px);
                padding-left: 17px;
            }
        }
    }
}
