.sns {
    // overflow: hidden;
    padding-bottom: 80px;
    @include mq("tablet"){
        padding-bottom: 100px;
    }
    &__wrapper {
        @include mq("tablet") {
            display: flex;
            // flex-wrap: wrap;
            justify-content: center;
            gap: 20px;
        }

        @include mq("bootstrap-lg") {
            gap: 60px;
        }
    }

    &__lg {
        @include mq("tablet", "max") {
            width: 100%;
            margin-bottom: 40px;
        }
    }

    &__iframewpr {

        @include mq("tablet") {
            display: flex;
            flex-direction: column;
            justify-content: center;
            gap: 20px;
        }

        @include mq("desktop") {
            flex-direction: row;
        }

        iframe {
            
            @include mq("tablet", "max") {
                height: 207px;
                width: 100%;
            }
        }
    }

    &__iframe {

        @include mq("desktop") {
            width: calc(100% / 2 - 20px / 2);
        }

        @include mq("bootstrap-lg") {
            width: auto;
        }

        &:not(:last-of-type) {
            @include mq("tablet", "max") {
                margin-bottom: 20px;
            }
        }
    }

    &__btns {
        max-width: 260px;

        @include mq("tablet", "max") {
            margin: 0 auto;
        }
    }

    h3 {
        font-size: 1.6rem;
        letter-spacing: 0;
        margin-bottom: 15px;
    }
}