.header {
    background-color: $color-lgt-blue;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    z-index: 7;

    &__logo {
        transition: all 0.25s ease;

        @include mq("tablet") {
            margin-left: 10px;

            &:hover,
            &:focus-visible {
                opacity: 0.7;
            }
        }

        @include mq("bootstrap-lg") {
            margin-left: 16px;
        }

        @include mq("desktop") {
            margin-left: 40px;
        }


        img {
            display: block;

            @include mq("tablet", "max") {
                width: 213px;
            }
        }
    }

    &__wrapper {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: -1px;
        
        @include mq("bootstrap-lg", "max") {
            gap: 20px;
        }

        @include mq("tablet", "max") {
            padding: 15px 20px 13px;
        }
    }

    &__rgt {
        background-color: $color-primary;
        position: fixed;
        top: 67px;
        left: 0;
        right: 0;
        height: 0;
        transition: height 0.3s ease;
        overflow: hidden;
        
        @include mq("tablet") {
            overflow: visible;
            height: unset;
            display: block;
            position: static;
            border-radius: 0 0 0 8px;
            padding: 10px 16px 0 16px;
            max-width: 803px;
            width: 100%;
        }
        
        @include mq("bootstrap-lg") {
            max-width: 695px;
            padding: 13px 40px 0 60px;
        }
        
        @include mq("desktop") {
            max-width: 803px;
            padding: 13px 40px 0 60px;
        }

        &.open {
            @include mq("tablet", "max") {
                height: calc(100vh - 67px);
                overflow-y: auto;
            }
        }

        &-wrapper {
            @include mq("tablet", "max") {
                display: flex;
                flex-direction: column-reverse;
                padding: 40px 22px 80px;
            }
        }
    }

    &__privacy {
        &-lnk {
            display: inline-block;
            font-size: 1.4rem;
            text-align: center;
            margin-top: 60px;
            margin-inline: auto;
            color: $color-white;
            text-decoration: none;
        }
    }

    &__top {
        display: flex;
        flex-direction: column;
        margin-bottom: 15px;

        @include mq("tablet") {
            flex-direction: row;
            justify-content: flex-end;
            align-items: center;
        }
    }

    &__lnk {
        font-size: 1.3rem;
        font-weight: 400;
        color: $color-white;
        text-decoration: none;
        transition: all 0.25s ease;

        @include mq("tablet", "max") {
            font-size: 1.6rem;
            padding: 20px 10px;
            line-height: 1.5;
            border-bottom: 1px solid $color-drk-blue3;
        }

        &.-sm {

            @include mq("tablet") {
                padding-right: 20px;
                line-height: 1.84;

                &:hover,
                &:focus-visible {
                    text-decoration: underline;
                }
            }

        }

        &.-lg {
            @include mq("tablet") {
                padding: 3px 11px 3px 11px;
                border: solid 1px $color-white;
                border-radius: 20px;

                &:hover,
                &:focus-visible {
                    background-color: $color-white;
                    color: $color-primary;
                }
            }

        }

        // a[target="_blank"] {
        //     position: relative;
        //     &:after {
        //         position: absolute;
        //         content: "";
        //     }
        // }
    }
}