html {
    font-size: 10px;
    //for footer position bottom
    height: calc(100% - 68px);
    @include mq("tablet") {
        height: calc(100% - 91px);
    }
}

body {
    font-family: $font-primary;
    font-size: 1.6rem;
    font-weight: 400;
    color: $color-black;
    line-height: 1.2;
    background: $color-lgt-blue;

    //for footer position bottom

    display: flex;
    flex-direction: column;
    min-height: 100%;
    &.hide-overflow {
        overflow: hidden;
    }
}

.container {
    display: block;
    max-width: 100%;
    padding: 0 20px;
    margin: 0 auto;

    @include mq("tablet") {
        padding: 0 20px;
        max-width: 1128px;
    }

    &.-sm {
        // max-width: 944px;
    }
    &.-lg {
        max-width: 1306px;
        padding: 0 0 0 30px;
        // margin-right: 0;
        // padding: 0;
    }

    &-sub {
        max-width: 1168px;
        margin-inline: auto;
        padding: 0;
    }
}

%reset-ul {
    margin: 0;
    padding: 0;
    list-style: none;
}

* {
    box-sizing: border-box;
}
