.parent-page {
    & > .container {
        padding: 0;
        margin: 0;
        margin-left: auto;
        max-width: calc(100% - 10px);

        @include mq("tablet") {
            // max-width: 100%;
            // margin-left: 36px;
            max-width: calc(100% - 36px);
        }

        // @media (min-width: 1367px) {
        //     margin-inline: auto;
        // }
    }

    .blockaside__lg {
        padding: 0;
        padding-bottom: 100px;

        .container-sub {
            position: relative;
        }
    }

    .component.-sm {
        padding: 58px 33px 0 22px;

        @include mq("tablet") {
            padding-inline: 40px;
            padding-top: 80px;
        }
    }

    .component h5 {
        font-size: 1.8rem;
        letter-spacing: 0.1em;
        margin-bottom: 10px;
        color: #fff;
        margin-left: 22px;
        @include mq("tablet"){
            margin-left: 0;
        }
        &::before {
            width: 16px;
            background-color: #fff;
        }
    }

    .services {
        padding-top: 120px;
        padding-bottom: 0;

        .container {
            max-width: 1088px;
            margin-inline: auto;
        }
    }

    &__main {
        border-top-left-radius: 20px;
        overflow: hidden;
        // line-height: 0;
        position: relative;
        background-image: url(../img/parent-page/parent-page.jpg);
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
        display: flex;
        justify-content: center;
        align-items: center;
        min-height: 300px;
        padding-bottom: 50px;
        padding-top: 50px;
        @include mq("tablet") {
            padding-bottom: 60px;
            padding-top: 60px;
        }
        // &::after {
        //     content: "";
        //     // background-image: url(.././img/parent-page/overlay.png);
        //     background-image: linear-gradient(to right, #2faee5, #005ca9);
        //     position: absolute;
        //     inset: 0;
        //     opacity: 0.5;

        //     @include mq("tablet") {
        //         background-image: linear-gradient(135deg, #8acfdc, #0e7bbe 50%, #003b88);
        //     }
        // }

        img {
            aspect-ratio: 1330 / 300;
            object-fit: cover;
            object-position: center;
            width: 100%;
            display: block;
            @include mq("tablet", "max") {
                height: 300px;
                object-position: center center;
            }
        }

        h1 {
            font-size: 2.6rem;
            font-weight: 500;
            color: $color-white;
            // position: absolute;
            // left: 0;
            // top: 50%;
            // transform: translate(0, -50%);
            z-index: 1;
            width: 100%;
            padding: 0 10px;
            // text-align: center;
            // text-shadow: 6px 6px 5px rgba(0, 0, 0, 0.8);
            //    width: 100%;
            //    text-align: center;

            @include mq("tablet") {
                font-size: 3.2rem;
                line-height: 1.33;
            }
            span {
                margin: 0 auto;
                display: block;
                width: fit-content;
            }
        }
        // > div:last-child{
        //     margin-bottom: 0;
        // }
    }

    &__menu {
        max-width: 311px;
        margin-inline: auto;
        margin-top: -50px;

        @include mq("phablet") {
            max-width: 1077px;
            padding-inline: 20px;
            // max-width: 522px;
        }

        @include mq("tablet") {
            margin-inline: auto;
            padding-inline: 20px;
            // margin-inline: 63px;
            margin-top: -67px;
        }

        @include mq("bootstrap-lg") {
            margin-inline: auto;
        }

        ul {
            // border-top: 1px solid $color-blue-2;
            border-left: 1px solid $color-blue-2;
            display: flex;
            flex-wrap: wrap;
        }

        ul li {
            width: 50%;
            flex: 0 0 50%;

            @include mq("phablet") {
                width: calc(100% / 3);
                flex: 0 0 calc(100% / 3);
            }

            @include mq("bootstrap-lg") {
                width: 25%;
                flex: 0 0 25%;
            }
            &:nth-of-type(1),
            &:nth-of-type(2) {
                border-top: solid 1px $color-blue-2;
            }
            &:nth-of-type(3) {
                @include mq("phablet") {
                    border-top: solid 1px $color-blue-2;
                }
            }
            &:nth-of-type(4) {
                @include mq("bootstrap-lg") {
                    border-top: solid 1px $color-blue-2;
                }
            }
        }
    }

    &__link {
        font-size: 1.4rem;
        line-height: 1.5;
        color: $color-black;
        background-color: #fff;
        // display: block;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        height: 100%;
        text-decoration: none;
        padding: 12px 23px 11px 10px;
        border-right: 1px solid $color-blue-2;
        border-bottom: 1px solid $color-blue-2;
        position: relative;
        transition: all 0.35s ease;
        @include mq("phablet") {
            padding: 12px 30px 11px 10px;
        }
        @include mq("tablet") {
            font-size: 1.6rem;
            padding: 18px 35px 18px 20px;
        }

        &::after {
            content: url(.././img/icons/arrow-right.svg);
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            right: 8px;
            transition: all 0.35s ease;

            @include mq("tablet") {
                right: 17px;
            }
        }

        @include mq("tablet") {
            &:hover {
                color: $color-primary;
                background-color: $color-lgt-blue-3;

                &::after {
                    right: 15px;
                }
            }
        }

        &.-nopadding {
            padding: 0;
            margin-top: 32px;
            display: inline-block;

            &::after {
                content: none;
            }
        }
    }
}

.child-page {
    // overflow: hidden;

    .blockcategory {
        margin-bottom: 0;
        position: sticky;
        top: 120px;
        @include mq("tablet", "max") {
            max-width: 271px;
            margin: 0 auto;
        }

        .component h5 {
            color: $color-black;
            margin-bottom: 40px;

            &::before {
                background-color: $color-black;
            }
        }
    }

    .blockaside__lg.-bgwhite {
        @include mq("bootstrap-lg", "max") {
            padding: 80px 40px 100px 40px;
        }

        @include mq("tablet", "max") {
            padding: 80px 32px 60px 22px;
        }
    }

    .container {
        padding: 0;
        margin: 0;
        margin-left: auto;
        max-width: calc(100% - 10px);

        @include mq("tablet") {
            max-width: 1306px;
            padding: 0;
            width: calc(100% - 30px);
        }

        @include mq("bootstrap-lg") {
            width: calc(100% - 60px);
        }

        @media (min-width: 1367px) {
            margin-inline: auto;
        }
    }

    &__ttl {
        font-family: $font-sec;
        font-size: 2.8rem;
        letter-spacing: 0.05em;
        line-height: 1.4;
        margin-bottom: 52px;
    }

    &__img {
        margin-bottom: 60px;

        img {
            aspect-ratio: 886/485;
            object-fit: cover;
            width: 100%;
        }
    }
}

.fixed-page {
    .pagettl-bar {
        @include mq("tablet", "max") {
            padding: 60px 20px 60px 20px;
        }
    }
    .blockaside__lg {
        border-radius: 20px;
    }

    .container {
        @include mq("tablet", "max") {
            padding: 0 10px;
        }
    }

    .blockaside__lg.-bgwhite {
        padding: 60px 40px 100px 40px;
        @include mq("tablet", "max") {
            padding: 60px 22px 100px;
        }
    }
}

.activity-report {
    .container {
        max-width: 934px;
    }

    .blockaside__lg.-bgwhite {
        padding: 60px 20px;
        border-radius: 0;

        @include mq("tablet") {
            padding: 60px 40px;
        }
    }

    .child-page__img {
        margin: 40px 0 0;

        img {
            aspect-ratio: auto;
        }
    }

    &__txt {
        font-size: 1.8rem;
        font-weight: 500;
        letter-spacing: 0.05em;
        line-height: 1.5;

        @include mq("tablet") {
            font-size: 2.4rem;
        }
    }

    &__inner {
        margin-top: 80px;

        @include mq("tablet") {
            padding-inline: 40px;
        }

        .navigation {
            margin-top: 80px;
        }
    }

    .pagettl-bar {
        padding-block: 60px 40px;
        margin-block: 0;

        @include mq("tablet") {
            padding-block: 40px;
        }
    }
}

.category {
    &__box {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        gap: 10px;
        border-bottom: 1px solid #c8ced3;
        padding-bottom: 20px;
        margin-bottom: 30px;
        // text-align: center;
    }

    &__name {
        font-size: 1.3rem;
        font-weight: 500;
        line-height: 1.96;
        padding: 3px 10px;
        color: $color-white;
        background-color: $color-primary;
        display: inline-block;
    }

    &__tag {
        font-size: 1.3rem;
        line-height: 1.96;
        color: $color-primary;
        display: inline-block;
    }
}

.notice {
    .container {
        max-width: 854px;
    }

    .child-page__img img {
        aspect-ratio: auto;
    }

    .category__name {
        background: linear-gradient(90deg, #2faee5 0%, #005ca9 100%);
        font-weight: 400;
        padding: 5px 8px;
        display: inline-block;
        margin-bottom: 22px;
        line-height: 1;
    }

    .blockaside__lg {
        padding-bottom: 80px;
        border-bottom: 1px solid #c8ced3;
    }

    .child-page__img {
        margin: 35px 0 0;

        @media (min-width: 768px) {
            margin: 40px 0 0;
        }
    }

    .activity-report__inner {
        padding-inline: 0;
    }

    .pagettl-bar {
        padding-block: 60px 40px;
        // margin: 0;

        @media (min-width: 768px) {
            padding-block: 40px 102px;
        }
    }
}
