    background-color: $color-lgt-blue-4;
    margin-top: -180px;
    padding-top: 160px;
    padding-bottom: 80px;
    @include mq("tablet"){
        padding-top: 180px;
    }
    &__img {
        display: block;
        max-width: 720px;
        margin: 0 auto;
        overflow: hidden;
        transition: all 0.25s ease;
        position: relative;
        img {
            transition: all 0.25s ease;
        }
